import { isElementEnabled } from '../../lib/isElementEnabled.js'
import { waitForNavigation } from '../../lib/puppeteer/waitForNavigation.js'

export async function navigateToNextPage (page) {
  const nextButton = await page.$('.pagination--page-next')
  if (await canNavigateToNextPage(nextButton)) {
    const nextButtonA = await nextButton.$('a')
    await Promise.all([
      waitForNavigation(page),
      nextButtonA.click(),
    ])
    await page.waitFor(() => {
      const $loading = document.querySelector('.loading')
      return !$loading.classList.contains('is')
    })
    return true
  } else {
    return false
  }
}

async function canNavigateToNextPage (nextButton) {
  return nextButton && await isElementEnabled(nextButton)
}
